input[type="checkbox"],
input[type="radio"] {
  display: none;
}
input[type="checkbox"]:checked + label,
input[type="checkbox"]:not(:checked) + label {
  transition: all 0.3s ease-in-out;
}
.switch_Word + label {
  background-color: #e6e6de;
  border-radius: 12px;
  cursor: pointer;
  display: inline-block;
  height: 24px;
  position: relative;
  width: 52px;
}
input[type="checkbox"]:checked + label:before,
input[type="checkbox"]:checked + label i:before,
input[type="checkbox"]:not(:checked) + label i:before,
input[type="checkbox"]:checked + label i:after,
input[type="checkbox"]:not(:checked) + label i:after,
input[type="checkbox"]:not(:checked) + label:before {
  transition: all 0.3s ease-in-out;
}
input[type="checkbox"]:not(:checked) + label:after {
  transition: all 0.3s ease-out;
}
.switch_Word:checked + label:before {
  content: '🌚';
  font-size: 20px;
  left: 2px;
  color: #fff;
}
.switch_Word + label:before {
  content: '🌝';
  height: 20px;
  width: 20px;
  color: #b4b4b4;
  font-size: 20px;
  position: absolute;
  top: -1px;
  left: 29px;
}
.switch_Word + label:after {
  border-radius: 50%;
  background-color: #fff;
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  top: 2px;
  left: 1px;
  /* box-shadow: 0.2px 0.2px 1px 0.5px rgb(180,180,180); */
}
.switch_Word + label {
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
input[type="checkbox"]:checked + label:after {
  transition: all 0.3s ease-in;
}
.switch_Word:checked + label:after {
  left: 30px;
}
